<template>
	<div class="pa-md-6 pa-sm-3">
		<v-card>
			<v-card-title class="py-2">Daftar Startup</v-card-title>
			<v-card-text>
				<Loader v-if="isLoading" />
				<v-data-table
					v-else
					class="mt-0"
					:headers="header"
					:items="dataStartup"
					:items-per-page="10">	
					<template v-slot:item.no="{item}">
						{{dataStartup.map(x => x.id).indexOf(item.id)+1}}
					</template>
					<template v-slot:item.startup="{item}">
						<div class="d-flex align-center">
							<!-- <v-avatar size="40">
								<img :src="require('@/assets/'+item.startup.logo)" alt="logo startup">
							</v-avatar> -->
							<div class="ml-3">
								{{ item.name }}
								<!-- <span class="body grey--text">{{ item.startup.bidang }}</span> -->
							</div>
						</div>
					</template>
					<template v-slot:item.incubator="{item}">
						<div class="d-flex align-center">
							<!-- <v-avatar size="40">
								<img :src="require('@/assets/'+item.incubator.logo)" alt="logo incubator">
							</v-avatar> -->
							<div class="ml-3">{{ item.incubator_by }}</div>
						</div>
					</template>
					<template v-slot:item.manage="{item}">
						<div class="d-flex align-center">
							<!-- <v-avatar size="40">
								<img :src="require('@/assets/'+item.manage.avatar)" alt="logo manage">
							</v-avatar> -->
							<div class="ml-3">
								{{ item.managed_by }} <br>
								<!-- <span class="body grey--text">{{ item.manage.jabatan }}</span> -->
							</div>
						</div>
					</template>
					<template v-slot:no-data>
						<v-alert type="info" class="mt-4">
							<div>Data tidak ditemukan</div>
						</v-alert>
					</template>
					<template v-slot:item.action="{item}">
						<v-btn icon color="primary">
							<v-icon small>mdi-eye</v-icon>
						</v-btn>
						<Hapus :id="item.id" />
					</template>		
				</v-data-table>
			</v-card-text>
		</v-card>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import Loader from '@/components/Loader'
	import Hapus from '@/components/Admin/Startup/Hapus'

	export default{
		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Startup', value: 'name', sortable: false },
				{ text: 'Managed By', value: 'managed_by', sortable: false },
				{ text: 'Incubator By', value: 'incubator_by', sortable: false },
				{ text: 'Action', value: 'action', align: 'center', sortable: false }
			],

			data: [
				{
					id: 1,
					incubator: {
						logo: 'avatar.jpg',
						name: 'Amikom Bussniess Park'
					},
					startup: {
						logo: 'avatar.jpg',
						name: 'Smartedu',
						bidang: 'Education'
					},
					manage: {
						avatar: 'avatar.jpg',
						name: 'Rizky Rosyid Hidayat',
						jabatan: 'CEO'
					}
				}
			],
		}),

		components: {
			Hapus,
			Loader
		},

		created() {
			this.$store.dispatch('dataStartup/getDataStartup')
			this.$store.dispatch('dataStartup/updateIsLoading', true)
		},

		computed: {
			...mapState('dataStartup', ['isLoading', 'dataStartup'])
		}
	}
</script>